import type { LiveProviderProps } from 'react-live'
import theme from 'prism-react-renderer/themes/synthwave84'
import React from 'react'
import { LiveEditor, LiveError, LivePreview, LiveProvider } from 'react-live'
import styled from 'styled-components'
import { transform } from 'sucrase'
import { scope } from './scope'

function transformCodeForDisplay(code: string = '') {
  return (
    code
      // Strips multiline comments, such as the open source header.
      .replace(/\/\*[^]*?\*\//g, '')
      // Removes imports because ReactLive can't deal with them.
      .replace(/^import\s.*/gm, '')
      // Conserve space!
      .trim()
  )
}

function transformCode(code: string = '') {
  try {
    // We reuse display transformations because many of them aren't compatible
    // with Reactlive either.
    code = transformCodeForDisplay(code)

    // The keyword export is a syntax error in ReactLive.
    code = code.replace(/export default /, 'const Default____Component = ')

    // ReactLive uses Sucrase, but does not enable the TypeScript transform. In
    // order to get our TS code to parse we must pre-parse TypeScript. If we do
    // not double-parse JSX, then it is a syntax error for Sucrase. ReactLive
    // has no plans to support TS either.
    //
    // See: https://github.com/FormidableLabs/react-live/issues/8.
    code = transform(code, { transforms: ['imports', 'jsx', 'typescript'] })
      .code

    // Using the noInline prop means we must manually rendera component. This
    // gives us greater flexibility over how ReactLive interprets the source
    // of our stories.
    code = `${code}\n\nrender(<Default____Component />)`
  } catch (e: unknown) {
    if (e instanceof Error) {
      // ReactLive does not handle errors when pre-transforming code.
      // See: https://github.com/FormidableLabs/react-live/issues/233.
      // Unfortunately this doesn't seem fixed. The above issue links off to
      // https://github.com/FormidableLabs/react-live/pull/285 which references
      // a commit that appears to fix it, but it doesn't seem to work in this
      // context, which we should investigate further when we have time.
      return `render(<>{\`${e.message.replace(/`/g, '\\`')}\`}</>)`
    }
  }
  return code
}

const StyledLiveEditor = styled(LiveEditor)`
  border-radius: 5px 5px 0 0;
`

const StyledLiveError = styled(LiveError)`
  border: 1px solid #e8e8e8;
  border-bottom: 0;
  border-top: 0;
  margin: 0;
  padding: 10px;
`

const StyledLivePreview = styled(LivePreview)`
  border: 1px solid #e8e8e8;
  border-radius: 0 0 5px 5px;
  border-top: 0;
  margin: 0;
  padding: 10px;
`

export interface EditorProps {
  code: LiveProviderProps['code']
  scope: LiveProviderProps['scope']
}

export const Editor = (props: EditorProps) => {
  return (
    <LiveProvider
      code={transformCodeForDisplay(props.code)}
      noInline
      scope={{ ...scope, ...props.scope }}
      theme={theme}
      transformCode={transformCode}
    >
      <StyledLiveEditor />
      <StyledLiveError />
      <StyledLivePreview />
    </LiveProvider>
  )
}
